














































export default 
  name: 'Snippet'
  props: [
    'song'
    'artist'
    'url'
    'startTime'
    'desc'
  ]
  computed:
    escName: -> @esc(@song) + @esc(@artist)
  methods:                   
    esc: (name) -> name.replace(/[^[^a-zA-Z_$]|[^\w$]/gi, "_").toLowerCase()
                          #      ^ matches stuff that shouldnt be in a js variable name
    restartVideo: -> window[@escName + "-player"].seekTo(@startTime || 0)
    startVideo: -> 
      @playing = true
      window[@escName + "-player"].playVideo()
      setTimeout window[@escName + "-player"].playVideo, 200
      setTimeout window[@escName + "-player"].playVideo, 300
    
    pauseVideo: -> 
      window[@escName + "-player"].pauseVideo()
      @playing = false
    youtube_parser: (url) ->
      regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/
      match = url.match(regExp)
      if match and match[7].length == 11 then match[7] else false

  data: ->
    ready: false
    playing: false
  mounted: ->
    window[@escName + "_instance"] = this
    window[@escName + "_init"] = (e) ->
      # Update the controls on load
      # window.updateTimerDisplay()
      # window.updateProgressBar()
      # Clear any old interval.
      window[e + "_instance"].restartVideo()
      window[e + "_instance"].startVideo()
      setTimeout (->
                  window[e + "_instance"].ready = true
                 ), 2000   
      setTimeout window[e + "_instance"].pauseVideo, 500
                
      clearInterval window.time_update_interval
      # Start interval to update elapsed time display and
      # the elapsed part of the progress bar every second.
      window.time_update_interval = setInterval((->
        # window.updateTimerDisplay()
        # window.updateProgressBar()
      ), 1000)
    
    window.updateTimerDisplay = ->
      # Update current time text display.
      # $('#current-time').text formatTime(player.getCurrentTime())
      # $('#duration').text formatTime(player.getDuration())
      @startVideo()
      @stopVideo()
      return

    window.formatTime = (time) ->
      time = Math.round(time)
      minutes = Math.floor(time / 60)
      seconds = time - (minutes * 60)
      seconds = if seconds < 10 then '0' + seconds else seconds
      minutes + ':' + seconds
    window.ytplayers.push(@escName + "_ready")
    window[@escName + "_ready"] = =>
      window[@escName + "-player"] = new (window.YT.Player)('video-placeholder-' + @esc(@song),
        width: 0.001
        height: 0.001 
        videoId: @youtube_parser(@url)
        playerVars:
          start: @startTime
        events: 
          onReady: (=> window[@escName + "_init"](@escName))
          onStateChange: window.onPlayerStateChange
          )
      return
    window.onPlayerStateChange = ->
      # console.log "change"
